"use client"

import { Card } from "@/components/ui/card"
import { ArrowRight, AlertCircle, CheckCircle2 } from "lucide-react"
import Link from "next/link"
import { useLanguage } from "@/lib/i18n-context"
import { useQuery } from "@tanstack/react-query"
import { dashboardApi } from "@/lib/api/dashboard"

export default function DashboardPage() {
  const { t } = useLanguage()

  const { data, isLoading } = useQuery({
    queryKey: ['dashboard'],
    queryFn: dashboardApi.getData,
  })

  // Fallback/Loading state
  if (isLoading) {
    return (
      <div className="p-8 bg-muted/20 min-h-screen flex items-center justify-center">
        <p>Loading dashboard...</p>
      </div>
    )
  }

  const stats = data?.stats || [
    { label: t('dashboard.stats.activeVehicles'), value: "0", icon: "ðŸš—" },
    { label: t('dashboard.stats.upcomingTasks'), value: "0", icon: "ðŸ“‹" },
    { label: t('dashboard.stats.taxRecords'), value: "0", icon: "ðŸ“„" },
  ]

  const upcomingTasks = data?.upcomingTasks || []
  const recentServices = data?.recentServices || []

  return (
    <div className="p-8 bg-muted/20 min-h-screen">
      <div className="max-w-6xl mx-auto space-y-8">
        {/* Header */}
        <div>
          <h1 className="text-3xl font-bold text-foreground">{t('dashboard.title')}</h1>
          <p className="text-muted-foreground mt-2">{t('dashboard.subtitle')}</p>
        </div>

        {/* Stats Cards */}
        <div className="grid md:grid-cols-3 gap-6">
          {stats.map((stat: { label: string; value: string; icon: string }, idx: number) => (
            <Card key={idx} className="p-6">
              <div className="flex items-start justify-between">
                <div>
                  <p className="text-sm font-medium text-muted-foreground mb-2">{stat.label}</p>
                  <p className="text-3xl font-bold text-foreground">{stat.value}</p>
                </div>
                <span className="text-2xl">{stat.icon}</span>
              </div>
            </Card>
          ))}
        </div>

        {/* Content Grid */}
        <div className="grid md:grid-cols-2 gap-6">
          {/* Upcoming Tasks */}
          <Card className="p-6">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-lg font-bold text-foreground">{t('dashboard.upcomingTasks')}</h2>
              <Link href="/app" className="text-sm text-blue-600 hover:text-blue-700">
                {t('dashboard.viewAll')}
              </Link>
            </div>
            {upcomingTasks.length === 0 ? (
              <div className="text-center py-8">
                <p className="text-muted-foreground text-sm">{t('dashboard.noTasks')}</p>
              </div>
            ) : (
              <div className="space-y-3">
                {upcomingTasks.map((task: { id: string; task: string; vehicle: string; date: string }) => (
                  <div
                    key={task.id}
                    className="flex items-start gap-3 p-3 bg-muted/50 rounded-lg border"
                  >
                    <AlertCircle className="w-5 h-5 text-yellow-600 mt-0.5 flex-shrink-0" />
                    <div className="flex-1 min-w-0">
                      <p className="font-medium text-foreground text-sm">{task.task}</p>
                      <p className="text-xs text-muted-foreground mt-1">{task.vehicle}</p>
                      <p className="text-xs text-muted-foreground mt-1">{task.date}</p>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </Card>

          {/* Recent Services */}
          <Card className="p-6">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-lg font-bold text-foreground">{t('dashboard.recentServices')}</h2>
              <Link href="/app/vehicles" className="text-sm text-blue-600 hover:text-blue-700">
                {t('dashboard.viewAll')}
              </Link>
            </div>
            {recentServices.length === 0 ? (
              <div className="text-center py-8">
                <p className="text-muted-foreground text-sm">{t('dashboard.noServices')}</p>
              </div>
            ) : (
              <div className="space-y-3">
                {recentServices.map((service: { id: string; service: string; vehicle: string; date: string }) => (
                  <div
                    key={service.id}
                    className="flex items-start gap-3 p-3 bg-muted/50 rounded-lg border"
                  >
                    <CheckCircle2 className="w-5 h-5 text-green-600 mt-0.5 flex-shrink-0" />
                    <div className="flex-1 min-w-0">
                      <p className="font-medium text-foreground text-sm">{service.service}</p>
                      <p className="text-xs text-muted-foreground mt-1">{service.vehicle}</p>
                      <p className="text-xs text-muted-foreground mt-1">{service.date}</p>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </Card>
        </div>

        {/* Empty State */}
        <Card className="p-12 text-center">
          <h2 className="text-2xl font-bold text-foreground mb-3">{t('dashboard.noVehicles')}</h2>
          <p className="text-muted-foreground mb-6">{t('dashboard.noVehiclesDesc')}</p>
          <Link
            href="/app/vehicles"
            className="inline-flex items-center gap-2 px-4 py-2 bg-gray-900 text-white rounded-lg hover:bg-gray-800 transition"
          >
            {t('dashboard.addVehicle')}
            <ArrowRight size={18} />
          </Link>
        </Card>
      </div>
    </div>
  )
}
